import styles from "./style.module.scss";
export default function Confirm({ closeToast, customMsg, fn }) {
	return (
		<div className={styles["confirm-wrapper"]}>
			<p className={styles["confirm-content"]}>
				Delete [ {customMsg} ] ?
			</p>
			<br />
			<div className={styles["btn-group"]}>
				<button
					className={`${styles["btn"]} ${styles["confirm"]}`}
					onClick={fn}
				>
					confirm
				</button>
				<button
					className={`${styles["btn"]} ${styles["demiss"]}`}
					onClick={closeToast}
				>
					demiss
				</button>
			</div>
		</div>
	);
}
