import { useState } from "react";
import { useEffect } from "react";
import { useRef } from "react";


function Input({ styleWrapper, onChangefn, ...props }) {
	const [value, setValue] = useState("");
	const inputRef = useRef();
	const handleKeyUp = (event) => {
		if (event.keyCode === 13) {
			if (event.target.value <= +event.target.max)
				onChangefn(event.target.value);
		}
	};
	useEffect(() => {
		if (props.autoFocus) inputRef.current.focus();
		let r = inputRef.current;

		r.addEventListener("keyup", handleKeyUp);
		return () => {
			r.removeEventListener("keyup", handleKeyUp);
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [props.autoFocus]);
	return (
		<div
			className={styleWrapper}
			onClick={(e) => {
				// chn ni bt => trnh bt cng s kin
				e.stopPropagation();
			}}
		>
			<input
				ref={inputRef}
				onChange={(e) => {
					e.stopPropagation();
					setValue(e.target.value);
				}}
				value={value}
				{...props}
			/>
		</div>
	);
}

export default Input;
