// render list of tags
// on item click => call setTag
import { nanoid } from "nanoid";
import { toast } from "react-toastify";
import { useState, useEffect, useContext, memo } from "react";
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import { Db } from "../../../database/Db_new";
import { sidebarContext } from "./../../../App";
import sidebarStyles from "./../Sidebar.module.scss";
import styles from "./tag.module.scss";
import Input from "./../../Input";
import Confirm from "../Confirm";
import Tippy from "@tippyjs/react/headless";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faTag,
	faTrashCan,
	faFilterCircleXmark,
} from "@fortawesome/free-solid-svg-icons";
import { useRef } from "react";

const toastRightPos = { POSITION: toast.POSITION.BOTTOM_RIGHT };
const Tp_wp = (props) => {
	return (
		<Tippy
			placement={props.position}
			interactive
			hideOnClick={false}
			render={({ ...attrs }) => {
				return (
					<div
						className={styles["tippy-wrapper"]}
						tabIndex='-1'
						{...attrs}
					>
						{props.children}
					</div>
				);
			}}
		>
			{props.parent_node}
		</Tippy>
	);
};
async function addTag(tag) {
	Db.transaction("rw", Db.tags, async function () {
		await Db.tags.add(tag);
	}).catch(function (e) {
		toast.error("tag already exists!", toastRightPos);
	});
}
async function DeleteItemFrom(db_name, searchKey, key) {
	await Db[db_name].where(searchKey).equals(key).delete();
}
async function getDatasFrom(db_name) {
	let data = await Db[db_name].orderBy("id").limit(15).toArray();
	return Promise.resolve(data);
}
async function Tagsfilter(db_name, str) {
	// lc tag c tn bt u vi ...
	let data = await Db[db_name].orderBy("id").filter((data) => {
		if (data.tag_name.startsWith(str)) return true;
		return false;
	});
	return Promise.resolve(data.toArray());
}
const handleConfirmTagDelete = (tag, cb) => {
	return (
		<Confirm
			customMsg={`${tag.tag_name}`}
			fn={async () => {
				await DeleteItemFrom("tags", "_id", tag._id).then(
					toast.info("tag deleted", toastRightPos)
				);
				await DeleteItemFrom("datas", "tag_id", tag._id).then(
					toast.info("datas deleted", toastRightPos)
				);

				await getDatasFrom("tags").then(cb);
			}}
		/>
	);
};

const TagRender = memo(
	({ items, activeItem, fn }) => {
		return (
			<>
				{!items.length ? (
					<span>empty</span>
				) : (
					items.map((tag) => (
						<ContextMenuTrigger key={tag._id} id={`#${tag._id}`}>
							<div
								className={`${styles["item-wapper"]} ${
									activeItem === tag._id
										? styles["active"]
										: ""
								}`}
							>
								<li
									className={styles["item"]}
									onClick={(e) => {
										e.stopPropagation();
										fn.setActiveItem(tag._id);
										fn.context.setTag(tag);
									}}
								>
									<span>{tag.tag_name}</span>

									<ContextMenu
										id={`#${tag._id}`}
										className={
											styles["context_menu_warpper"]
										}
									>
										<MenuItem
											className={
												styles["context_menu_item"]
											}
											onClick={async (e) => {
												e.stopPropagation();
												toast(
													handleConfirmTagDelete(
														tag,
														fn.setTags
													)
												);
											}}
										>
											<FontAwesomeIcon
												color='#b91c1c'
												icon={faTrashCan}
											/>
										</MenuItem>
									</ContextMenu>
								</li>
							</div>
						</ContextMenuTrigger>
					))
				)}
			</>
		);
	},
	(prevProps, nextProps) => {
		/* nextProps bng prevProps tr v true (khong re-render), ngc li tr v false (rerender)*/
		let match = prevProps.items.length === nextProps.items.length;
		if (match) {
			match = prevProps.items[0]._id === nextProps.items[0]._id;
			if (prevProps.activeItem !== nextProps.activeItem) {
				return false;
			}
			return match;
		}
		return false;
	}
);
export default function SidebarTags() {
	const context = useContext(sidebarContext);
	const [activeItem, setActiveItem] = useState();
	// const [showInput, setShowInput] = useState({});
	const [tags, setTags] = useState([]);
	const tmp_text_fillter = useRef("");
	useEffect(() => {
		getDatasFrom("tags").then(setTags);
	}, []);

	return (
		<div className={styles["wrapper"]}>
			<ContextMenuTrigger id='#create_tag'>
				<ul className={styles["list-wrapper"]}>
					<TagRender
						items={tags}
						activeItem={activeItem}
						fn={{ setActiveItem, setTags, context }}
					/>
				</ul>
			</ContextMenuTrigger>

			{
				// eslint-disable-next-line react/jsx-pascal-case
				<Tp_wp
					position='left'
					parent_node={
						<div
							className={styles["Tp_parentNode"]}
							title='tag filter with ...'
						>
							<FontAwesomeIcon icon={faFilterCircleXmark} />
						</div>
					}
				>
					<div className={styles["Tp_input_wp"]}>
						<Input
							tabIndex={-1}
							styleWrapper={sidebarStyles["input-wrraper"]}
							className={sidebarStyles["input"]}
							type='text'
							autoFocus
							placeholder='filter : Qx'
							onBlur={(e) => {
								let v = e.target.value.trim();
								// chi loc neu tu khoa khac voi truoc do
								// tmp_text_fillter : luu lai tim kiem hien tai de so sanh voi tim kiem tiep theo
								if (
									v.length >= 2 &&
									v !== tmp_text_fillter.current
								) {
									console.log("ok!");
									tmp_text_fillter.current = v;
									Tagsfilter("tags", v).then(setTags);
								}
							}}
						/>
					</div>
				</Tp_wp>
			}
			{
				// eslint-disable-next-line react/jsx-pascal-case
				<Tp_wp
					position='left'
					parent_node={
						<div
							className={styles["Tp_parentNode"]}
							title='create a new tag'
						>
							<FontAwesomeIcon icon={faTag} />
						</div>
					}
				>
					<div className={styles["Tp_input_wp"]}>
						<Input
							tabIndex={-1}
							styleWrapper={sidebarStyles["input-wrraper"]}
							className={sidebarStyles["input"]}
							type='text'
							placeholder={"new tag"}
							onBlur={async (e) => {
								e.stopPropagation();
								if (e.target.value.trim().length > 1) {
									addTag({
										_id: nanoid(5),
										tag_name: e.target.value,
										QTestArrayID: [],
									})
										.then(async () => {
											e.target.value = "";
											let data = await Db.tags
												.orderBy("id")
												.toArray();
											setTags(data);
										})
										.catch(console.error);
								}
							}}
						/>
					</div>
				</Tp_wp>
			}
		</div>
	);
}
